import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Main from './components/Main';
import Menu from './components/Offcanvas';
import Whowearepage from './components/Whowearepage';
import Whatwedopage from './components/Whatwedopage';
import Howweworkpage from './components/Howweworkpage';
import Expertisepage from './components/Expertisepage';
import Projectspage from './components/Projectspage';
import logo from './images/atkins_logo_black.svg';
import building from './images/building_design.svg';
import './App.css';
import './slick.css';
import './slick-theme.css';

const Atkins = () => (

  <div className="App">
    <Router onUpdate={() => window.scrollTo(0, 0)} >
      <div id="outer-container">
          <Menu pageWrapId={ "page-wrap" } outerContainerId={ "outer-container" } noOverlay/>
          <main id="page-wrap">

          <header className="App-header text-center">
            
            <div className="grid-container">
              <div className="grid-x grid-margin-x align-center">
                <div className="cell small-6 large-4">
                  <img src={building} className="building" alt="building design" />
                </div>
                <div className="cell small-6 large-4">
                  <Link to="/"><img src={logo} className="App-logo" alt="logo" /></Link>
                </div>
                <div className="cell small-12 medium-12 large-8">
                  <ul className="nav show-for-large">
                    <li>
                      <Link to="/">Home</Link>
                    </li>
                    <li>
                      <Link to="/whoweare">Who we are</Link>
                    </li>
                    <li>
                      <Link to="/whatwedo">What we do</Link>
                    </li>
                    <li>
                      <Link to="/howwework">How we work</Link>
                    </li>
                    <li>
                      <Link to="/expertise">Our expertise</Link>
                    </li>
                    <li>
                      <Link to="/projects">Projects</Link>
                    </li>
                  </ul>
                  
                </div>
              </div>
            </div>
           
          </header>

          <Route exact path="/" component={Home} />
          <Route path="/whoweare" component={Whoweare} />
          <Route path="/whatwedo" component={Whatwedo} />
          <Route path="/howwework" component={Howwework} />
          <Route path="/expertise" component={Expertise} />
          <Route path="/projects" component={Projects} />
          <footer className="text-center">
            
          </footer>
          </main>
        </div>
    </Router>
  </div>
);

const Home = () => (
    <Main />
);

const Whoweare = ({ match }) => (
  <Whowearepage />
);

const Whatwedo = ({ match }) => (
  <Whatwedopage />
);

const Howwework = ({ match }) => (
  <Howweworkpage />
);

const Expertise = ({ match }) => (
  <Expertisepage />
);

const Projects = ({ match }) => (
  <Projectspage />
);

export default Atkins;




