import React, { Component } from "react";
import { Link } from "react-router-dom";
import { push as Menu } from 'react-burger-menu'

class Offcanvas extends Component {

  constructor (props) {
    super(props)
    this.state = {
      menuOpen: false
    }
  }

  showSettings (event) {
    event.preventDefault(); 
  }

  // This keeps your state in sync with the opening/closing of the menu
  // via the default means, e.g. clicking the X, pressing the ESC key etc.
  handleStateChange (state) {
    this.setState({menuOpen: state.isOpen})  
  }
  
  // This can be used to close the menu, e.g. when a user clicks a menu item
  closeMenu () {
    this.setState({menuOpen: false})
  }

  // This can be used to toggle the menu, e.g. when using a custom icon
  // Tip: You probably want to hide either/both default icons if using a custom icon
  // See https://github.com/negomi/react-burger-menu#custom-icons
  toggleMenu () {
    this.setState({menuOpen: !this.state.menuOpen})
  }

  render () {
    return (
      <Menu isOpen={this.state.menuOpen} onStateChange={(state) => this.handleStateChange(state)}>
        <ul className="mobile-nav vertical menu">
              <li><Link to="/" onClick={() => this.closeMenu()}>Home</Link><div className="nav-divider"></div></li>
              <li><Link to="/whoweare" onClick={() => this.closeMenu()}>Who we are</Link><div className="nav-divider"></div></li>
              <li><Link to="/whatwedo" onClick={() => this.closeMenu()}>What we do</Link><div className="nav-divider"></div></li>
              <li><Link to="/howwework" onClick={() => this.closeMenu()}>How we work</Link><div className="nav-divider"></div></li>
              <li><Link to="/expertise" onClick={() => this.closeMenu()}>Our expertise</Link><div className="nav-divider"></div></li>
              <li><Link to="/projects" onClick={() => this.closeMenu()}>Projects</Link><div className="nav-divider"></div></li>
            </ul>
      </Menu>
    );
  }
}
export default Offcanvas;


