import React from "react";
import Slider from "react-slick";

class SimpleSlider extends React.Component {
  render() {
    var settings = {
      dots: true,
      arrows: true,
      infinite: true,
      speed: 500,
      autoplay: true,
      autoplaySpeed: 3000,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <Slider {...settings}>
        <div>
          <div className="homepage-header slide1">
            <div className="grid-container slide-content">
              <div className="grid-x grid-margin-x">
                <h1 className="strapline">Lorem Ipsum Dolor</h1>
                <p>We create outstanding places and spaces that respond to the needs and resources of a contemporary world</p>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div className="homepage-header slide2">
            <div className="grid-container slide-content">
              <div className="grid-x grid-margin-x">
                <h1 className="strapline">Lorem Ipsum Dolor</h1>
                <p>We create outstanding places and spaces that respond to the needs and resources of a contemporary world</p>
             </div>
            </div>
          </div>
        </div>
        <div>
          <div className="homepage-header slide3">
            <div className="grid-container slide-content">
              <div className="grid-x grid-margin-x">
                <h1 className="strapline">Lorem Ipsum Dolor</h1>
                <p>We create outstanding places and spaces that respond to the needs and resources of a contemporary world</p>
             </div>
            </div>
          </div>
        </div>
      </Slider>
    );
  }
}
export default SimpleSlider;


